import React, { ReactNode } from 'react';
import { makeStyles, Theme, Paper } from '@material-ui/core';
import AnimatedListItem from 'src/components/Animated/AnimatedListItem';

const useStyles = makeStyles<Theme>(({ spacing, palette, breakpoints }) => ({
  root: {
    overflow: 'hidden',
    position: 'relative',
  },
  image: {
    objectFit: 'cover',
    width: '100%',
    verticalAlign: 'middle',
    [breakpoints.down('md')]: {
      height: 200,
    },
  },
  coverWrapper: {
    position: 'absolute',
    top: 0,
    left: 0,
    width: '100%',
    height: '100%',
    opacity: 0,
    transitionProperty: 'opacity, transform, filter',
    transitionDuration: '0.25s',
    transitionTimingFunction: 'ease-in-out',
    '&:hover': {
      opacity: 1,
    },
  },
  filterBox: {
    filter: 'blur(5px) brightness(0.75)',
    '&:hover': {
      transform: 'scale(1.1)',
    },
  },
  content: {
    position: 'absolute',
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'space-between',
    top: 0,
    left: 0,
    width: '100%',
    height: '100%',
    padding: spacing(2),
    color: palette.common.white,
  },
}));

interface Props {
  image: string;
  children: ReactNode;
}
const Card = ({ image, children }: Props) => {
  const styles = useStyles();

  return (
    <AnimatedListItem>
      <Paper className={styles.root}>
        <img className={styles.image} src={image} alt="카드 이미지" />
        <div className={styles.coverWrapper}>
          <div className={` ${styles.filterBox}`}>
            <img className={styles.image} src={image} alt="카드 이미지 필터" />
          </div>
          <div className={`${styles.content}`}>{children}</div>
        </div>
      </Paper>
    </AnimatedListItem>
  );
};

export default Card;
